<template>
    <div class='search'>
        <h1> Welcome to NASA Image Search  </h1>
        <form v-on:submit.prevent="getResult(query)">
            <label for="user">Search</label>
            <br>
            <input type="text"  id="user"  placeholder="enter the keyword" v-model="query">
            <br>
            <!-- <button @click="onclick()">Download image</button> -->
        </form>
        <div v-if="results" id="images">

            <div  v-for ="result in results" :key="result.id ">
                <img v-bind:src="result.links[0].href" alt="images" />

            </div>

        </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    name: "search",
    data ( ) {
        return {
            msg : 'search',
            query: ' ',
            results : false,
            filelink: ' ',
            address: 'http://localhost:8080/pic5.png',
        }
    }, methods :{

        getResult(query) {

            axios.get('https://images-api.nasa.gov/search?q='  + query +'&media_type=image'). then( response => {
                console.log(response.data.collection.items);
                this.results = response.data.collection.items;
                console.log(response.data.collection.items[0].links[0].href)

            });
        },
      downloadWithAxios(address){
        axios({
            method: 'get',
            url: address,
            responseType: 'arraybuffer'
        })
        .then(response => {

            this.forceFileDownload(response)

        })
        .catch(() => console.log('error occured'))
      }
    }
}
</script>
